___CSS_LOADER_EXPORT___.push([module.id, `/* Контейнер селекта */
.select-container {
  position: relative;
  display: inline-block; /* Или block, если нужно на всю ширину */
  width: 200px; /* Пример ширины */
  margin: 5px;
}

/* Лейбл над селектом */
.select-label {
  display: block;
  margin-bottom: 0.3rem;
  font-size: 0.9rem;
  color: #333;
  font-weight: 500;
}

/* Кнопка селекта */
.select-button {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Размещаем значение слева, стрелку справа */
  width: 100%;
  padding: 0.6rem 0.8rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 4px;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
  text-align: left;
  box-sizing: border-box;
}

.select-button:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.25);
}

/* Отображаемое значение или плейсхолдер */
.select-value {
  overflow: hidden;
  word-break: break-word;
}
.select-placeholder {
  color: #6c757d; /* Серый цвет плейсхолдера */
}

/* Стрелка */
.select-arrow {
  margin-left: 8px;
  color: #6c757d;
  transition: transform 0.2s ease-in-out;
}
/* Поворот стрелки при открытом списке */
.select-button[aria-expanded="true"] .select-arrow {
    transform: rotate(180deg);
}


/* Выпадающий список */
.select-listbox {
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  padding: 0.5rem 0;
  max-height: 200px; /* Ограничение высоты с прокруткой */
  overflow-y: auto;
  z-index: 1050; /* Должен быть выше другого контента */
  box-sizing: border-box;
}

/* Опция в списке */
.select-option {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  cursor: pointer;
  white-space: normal;
  overflow: hidden;
  color: #212529;
  background-color: transparent;
  border: 0;
  transition: background-color 0.15s ease-in-out, color 0.15s ease-in-out;
}

/* Активная опция (при навигации клавиатурой) */
.select-option--active {
  color: #fff;
  background-color: #007bff;
}
.select-option--active .select-option-checkmark {
    color: #fff; /* Белая галочка на синем фоне */
}


/* Выбранная опция */
.select-option--selected {
  font-weight: 600;
  /* background-color: #e9ecef; /* Можно добавить легкий фон */
}

/* Галочка для выбранной опции */
.select-option-checkmark {
    margin-left: 10px;
    color: #007bff; /* Цвет галочки */
}

/* Отключенная опция */
.select-option--disabled {
  color: #6c757d;
  cursor: not-allowed;
  background-color: transparent;
}
.select-option--disabled:hover,
.select-option--disabled.select-option--active {
    color: #6c757d;
    background-color: transparent; /* Убираем подсветку при наведении/активности */
}


/* Отключенный селект */
.select-container--disabled .select-button {
  background-color: #e9ecef;
  opacity: 0.65;
  cursor: not-allowed;
} `, "",{"version":3,"sources":["webpack://./src/components/Select/Select.css"],"names":[],"mappings":"AAAA,sBAAsB;AACtB;EACE,kBAAkB;EAClB,qBAAqB,EAAE,wCAAwC;EAC/D,YAAY,EAAE,kBAAkB;EAChC,WAAW;AACb;;AAEA,uBAAuB;AACvB;EACE,cAAc;EACd,qBAAqB;EACrB,iBAAiB;EACjB,WAAW;EACX,gBAAgB;AAClB;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B,EAAE,6CAA6C;EAC7E,WAAW;EACX,sBAAsB;EACtB,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,sBAAsB;EACtB,4BAA4B;EAC5B,yBAAyB;EACzB,kBAAkB;EAClB,wEAAwE;EACxE,eAAe;EACf,gBAAgB;EAChB,sBAAsB;AACxB;;AAEA;EACE,qBAAqB;EACrB,UAAU;EACV,6CAA6C;AAC/C;;AAEA,0CAA0C;AAC1C;EACE,gBAAgB;EAChB,sBAAsB;AACxB;AACA;EACE,cAAc,EAAE,4BAA4B;AAC9C;;AAEA,YAAY;AACZ;EACE,gBAAgB;EAChB,cAAc;EACd,sCAAsC;AACxC;AACA,wCAAwC;AACxC;IACI,yBAAyB;AAC7B;;;AAGA,sBAAsB;AACtB;EACE,sBAAsB;EACtB,qCAAqC;EACrC,kBAAkB;EAClB,6CAA6C;EAC7C,iBAAiB;EACjB,iBAAiB,EAAE,oCAAoC;EACvD,gBAAgB;EAChB,aAAa,EAAE,sCAAsC;EACrD,sBAAsB;AACxB;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,oBAAoB;EACpB,eAAe;EACf,mBAAmB;EACnB,gBAAgB;EAChB,cAAc;EACd,6BAA6B;EAC7B,SAAS;EACT,uEAAuE;AACzE;;AAEA,+CAA+C;AAC/C;EACE,WAAW;EACX,yBAAyB;AAC3B;AACA;IACI,WAAW,EAAE,gCAAgC;AACjD;;;AAGA,oBAAoB;AACpB;EACE,gBAAgB;EAChB,4DAA4D;AAC9D;;AAEA,gCAAgC;AAChC;IACI,iBAAiB;IACjB,cAAc,EAAE,iBAAiB;AACrC;;AAEA,sBAAsB;AACtB;EACE,cAAc;EACd,mBAAmB;EACnB,6BAA6B;AAC/B;AACA;;IAEI,cAAc;IACd,6BAA6B,EAAE,+CAA+C;AAClF;;;AAGA,uBAAuB;AACvB;EACE,yBAAyB;EACzB,aAAa;EACb,mBAAmB;AACrB","sourcesContent":["/* Контейнер селекта */\n.select-container {\n  position: relative;\n  display: inline-block; /* Или block, если нужно на всю ширину */\n  width: 200px; /* Пример ширины */\n  margin: 5px;\n}\n\n/* Лейбл над селектом */\n.select-label {\n  display: block;\n  margin-bottom: 0.3rem;\n  font-size: 0.9rem;\n  color: #333;\n  font-weight: 500;\n}\n\n/* Кнопка селекта */\n.select-button {\n  display: flex;\n  align-items: center;\n  justify-content: space-between; /* Размещаем значение слева, стрелку справа */\n  width: 100%;\n  padding: 0.6rem 0.8rem;\n  font-size: 1rem